/* ‚úÖ Î°± Ïàè Î∏îÎ†åÎìú Ï†ÑÎ∂Ä Îã§ Ï†ÑÏ≤¥ Î∞∞Í≤Ω Î∞è ÌÖçÏä§Ìä∏ ÏÑ§Ï†ï */
#root, body {
  margin: 0;  
  padding: 0;
  font-family: 'Baloo 2', 'Comic Sans MS', cursive;
  min-height: 100vh;  
  background-color:  #f4f4f4;
  background-attachment: scroll;
  background-repeat: no-repeat;
  background-size: cover;
}

/* ‚úÖ Short Vowels Î©îÏù∏ Ïª®ÌÖåÏù¥ÎÑà */
.short-vowels-wrapper {
  margin: 0 auto;
  background-color: transparent;
  position: relative;
  text-align: center;
  margin-top: 80px;
}

/* ‚úÖ Î°úÍ≥† ÏïÑÏù¥ÏΩò (ÌïÑÏöîÌïú Í≤ΩÏö∞Îßå) */
.hero-icon {
  width: 80px;
  height: auto;
  position: absolute;
  top: 30px;
  left: 40px;
  filter: drop-shadow(2px 2px 4px rgba(0,0,0,0.15));
}


/* ‚úÖ ÌÉÄÏù¥ÌãÄ ÌÖçÏä§Ìä∏ */
.header-area h1 {
  font-size: 6rem;
  color: #383737;
  margin-bottom: 15px;
  font-weight: 900;
  text-shadow: 2px 2px #fbbbbb;
}

/* ‚úÖ Î™®Ïùå Î∞ïÏä§ Í∑∏Î¶¨Îìú Ï†ÑÏ≤¥Î•º Í∞êÏã∏Îäî Í∑∏Î¶¨Îìú Ïª®ÌÖåÏù¥ÎÑà */
.vowel-box-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 36px;
  padding: 40px 10px;
} 

/* ‚úÖ Ìë∏ÌÑ∞ */
.short-footer {
  margin-top: 80px;
  padding-top: 30px;
  border-top: 2px dashed #90caf9;
  font-size: 0.95rem;
  color: #607d8b;
  font-style: italic;
}

/* ‚úÖ Ïï†ÎãàÎ©îÏù¥ÏÖò */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
