/* ‚úÖ ÌéòÏù¥ÏßÄ Î∞∞Í≤ΩÎßå Îî∞Î°ú ÏÑ§Ï†ï */
.long-page-wrapper {
  background: #f4f4f4;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
}

/* ‚úÖ long Ï†ÑÏö© wrapper */
.long-vowels-wrapper {
  margin: 0 auto;
  margin-top: 80px;
  text-align: center;
}

/* ‚úÖ long Ï†ÑÏö© Ìó§Îçî ÏòÅÏó≠ */
.header-area {
  margin-bottom: 100px;
  padding-top: 10px;
  animation: fadeInDown 1s ease;
}

.header-area h1 {
  font-size: 6rem;
  color: #3b3b3b;
  font-weight: 900;
  text-shadow: 2px 2px #bbdefb;
  margin-bottom: 15px;
}



/* ‚úÖ Î†àÏù¥ÏïÑÏõÉÏùÑ ÏúÑÌïú Î∞ïÏä§ Í∑∏Î¶¨Îìú (Í≥µÌÜµ ÏïÑÎãò) */
.vowel-box-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 36px;
  padding: 40px 10px;
}

/* ‚úÖ Ïï†ÎãàÎ©îÏù¥ÏÖò */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
