/* ‚úÖ Ï†ÑÏó≠ Ïä§ÌÉÄÏùº */
body {
  margin: 0;
  background-color: #fefae0; /* Ï†ÑÏ≤¥ Î∞∞Í≤ΩÏÉâ */
  font-family: 'Comic Sans MS', cursive, sans-serif;
}

/* ‚úÖ Ï†ÑÏ≤¥ ÌéòÏù¥ÏßÄ ÎûòÌçº (Navbar Ìè¨Ìï®) */
.shorta-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}

/* ‚úÖ ÏΩòÌÖêÏ∏† Î†àÏù¥ÏïÑÏõÉ: ÏÇ¨Ïù¥ÎìúÎ∞î + Î≥∏Î¨∏ ÎÇòÎûÄÌûà */
.shorta-layout {
  display: flex;
  flex-grow: 1;
}

/* ‚úÖ ÏôºÏ™Ω ÏÇ¨Ïù¥ÎìúÎ∞î */
.shorta-sidebar {
  width: 240px;
  background-color: #f4fbff;
  padding: 50px 1rem 1rem 1rem;
  box-sizing: border-box;
  height: calc(100vh - 80px);  /* ‚úÖ ÌôîÎ©¥ ÏïÑÎûòÍπåÏßÄ + ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ÎÜíÏù¥ Ï†úÏô∏ */
  position: fixed;
  top: 80px; /* ‚úÖ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ÏïÑÎûòÎ°ú */
  left: 0;
  overflow-y: auto;
  border-right: 1px solid #dde6ee;
  z-index: 10;
}


/* ‚úÖ Ïò§Î•∏Ï™Ω ÏΩòÌÖêÏ∏† */
.shorta-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 60px 30px;
  box-sizing: border-box;
  margin-left: 240px;
}

/* ‚úÖ Î©îÎâ¥/Îã®Ïñ¥ Î∏îÎ°ù */
.shorta-menu-block:hover .shorta-word-list {
  display: block;
}

.shorta-menu-block {
  position: relative;
  margin-bottom: 25px;
  transition: all 0.3s ease;
}
.shorta-menu-block:hover {
  margin-bottom: 35px;
}

/* ‚úÖ Î©îÎâ¥ Ïä§ÌÉÄÏùº */
.shorta-vowel-menu {
  font-size: 2rem;
  font-weight: bold;
  padding: 12px 10px;
  cursor: pointer;
  background:  #d0ecff;
  border-radius: 10px;
  text-align: center;
  transition: background-color 0.3s;
}
.shorta-vowel-menu:hover {
  background-color: #b9dcf7;
}
.shorta-vowel-menu a {
  color: #000;
  text-decoration: none;
  font-weight: bold;
}

/* ‚úÖ ÌïòÏúÑ Îã®Ïñ¥ Î¶¨Ïä§Ìä∏ */
.shorta-word-list {
  display: none;
  margin-top: 10px;
  background: #fffef2;
  padding: 15px 20px;
  border-radius: 10px;
  box-shadow: 2px 4px 12px rgba(0, 0, 0, 0.1);
}
.shorta-word-list li {
  list-style: none;
  margin: 10px 0;
}
.shorta-word-list li a {
  font-size: 2rem;
  color: #3e4047;
  font-weight: bold;
  text-decoration: underline;
}

/* ‚úÖ Î©îÏù∏ Ïù¥ÎØ∏ÏßÄ */
.shorta-main-image {
  width: 600px;
  max-width: 95%;
  margin-bottom: 50px;
  border-radius: 25px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

/* ‚úÖ Îã®Ïñ¥ ÌÖçÏä§Ìä∏ */
.shorta-word {
  font-size: 5rem;
  color: #ff4d4d;
  margin-bottom: 40px;
  font-family: 'Comic Sans MS', cursive;
}

/* ‚úÖ Îã§Ïö¥Î°úÎìú ÎßÅÌÅ¨ Î≤ÑÌäº */
.shorta-download-link {
  background-color: #ff9e9e;
  color: white;
  font-size: 1.8rem;
  font-weight: bold;
  text-decoration: none;
  padding: 20px 40px;
  border-radius: 20px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s;
}
.shorta-download-link:hover {
  background-color: #ff6f6f;
}

/* ‚úÖ Î≤ÑÌäº Ïä§ÌÉÄÏùº ÌÜµÏùº */
.shorta-word-button,
.shorta-sound-button,
.shorta-download-button {
  font-size: 1rem;
  padding: 0.7rem 1.4rem;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.1);
  font-family: 'Arial', sans-serif;
  display: inline-flex;
  align-items: center;
  gap: 0.4rem;
  border: none;
  cursor: pointer;
  font-weight: normal;
  transition: transform 0.2s ease;
}

.shorta-word-button:hover,
.shorta-sound-button:hover,
.shorta-download-button:hover {
  transform: scale(1.05);
}

.shorta-sound-button {
  background-color: #aed581;
  color: #000;
}
.shorta-sound-button:hover {
  background-color: #9ccc65;
}

.shorta-download-button {
  background-color: #fff176;
  color: #000;
}
.shorta-download-button:hover {
  background-color: #ffee58;
}

/* ‚úÖ ÎÇ¥ÎπÑÍ≤åÏù¥ÏÖò (Navbar) */
.shorta-main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 40px;
  border-radius: 0 0 20px 20px;
  font-family: 'Baloo 2', 'Comic Sans MS', cursive;
  background-color: transparent;
  box-shadow: none;
}

.shorta-logo-area .shorta-logo-text {
  font-size: 3.5rem;
  color: #212224;
  font-weight: bold;
  text-decoration: none;
}

.shorta-nav-links {
  display: flex;
  gap: 40px;
  list-style: none;
}

.shorta-nav-links a {
  text-decoration: none;
  font-size: 2rem;
  color: #000;
  font-weight: bold;
}
.shorta-nav-links a:hover {
  color: #333536;
}
